'use client'

import { Avatar, AvatarImage, AvatarFallback } from "@/components/ui/avatar";
import {
  NavigationMenu,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
  NavigationMenuTrigger,
} from "@/components/ui/navigation-menu";
import { NavigationMenuContent } from "@radix-ui/react-navigation-menu";
import Link from "next/link";

import { Button } from "@/components/ui/button";
import Solutions from "./solutions/solutions";
import Company from "./company/company";
import Capabilities from "./capabilities/capabilities";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet"
import { useState } from "react";
import { Menu } from "lucide-react";


// Navbar

export default function Navigation() {
  const[isOpen, setIsOpen] = useState(false)
  return (
    <nav className="w-full bg-white ">
      <div className="container mx-auto flex items-center justify-between min-h-[80px] px-4">
        {/* Logo */}
        <div className="w-1/5 flex items-center gap-2 bg-red-200">
          <img src="/webstacks-logo-dark.svg" alt="" />
          <Avatar>
            <AvatarImage
              src="D:\Next.Js\webstacks\app\UI\webstacks-brand-asset\webstacks-logo-dark.svg"
              // src="..\..\UI\webstacks-brand-asset\webstacks-logo-dark.svg"
              alt="Logo"
            />
          </Avatar>
          {/* <AvatarFallback>Logo</AvatarFallback> */}
          <h2 className="text-lg font-semibold">Webstacks</h2>
        </div>

        {/* Navigation Menu */}
        <div className="flex-1 mx-4">
          <NavigationMenu className="w-full">
            <NavigationMenuList className="flex flex-row justify-center items-center gap-6">
              <NavigationMenuItem>
                <NavigationMenuTrigger>
                  <Link href="/">Capabilities</Link>
                </NavigationMenuTrigger>
                <NavigationMenuContent className="min-w-[20rem] w-[40rem] bg-white shadow p-4 flex gap-8">
                  <Capabilities />
                </NavigationMenuContent>
              </NavigationMenuItem>

              <NavigationMenuItem>
                <NavigationMenuTrigger>
                  <Link href="#">Solutions</Link>
                </NavigationMenuTrigger>
                <NavigationMenuContent className="min-w-[20rem] w-[40rem] bg-white shadow p-4 flex">
                  <Solutions />
                </NavigationMenuContent>
              </NavigationMenuItem>

              <NavigationMenuItem>
                <NavigationMenuLink asChild>
                  <Link href="#">Client Stories</Link>
                </NavigationMenuLink>
              </NavigationMenuItem>

              <NavigationMenuItem>
                <NavigationMenuLink asChild>
                  <Link href="#">Blog</Link>
                </NavigationMenuLink>
              </NavigationMenuItem>

              <NavigationMenuItem>
                <NavigationMenuTrigger>
                  <Link href="#">Company</Link>
                </NavigationMenuTrigger>
                <NavigationMenuContent className="min-w-[20rem] w-[40rem] bg-white shadow p-4 flex">
                  <Company />
                </NavigationMenuContent>
              </NavigationMenuItem>
            </NavigationMenuList>
          </NavigationMenu>
        </div>

        {/* Button */}
        <div className="w-auto">
          <Button className={"bg-[#0a49eb] hover:bg-[#0f3bbe] cursor-pointer"}>
            Book Intro Call
          </Button>
        </div>

        {/* Mobile Navigation */}
        <div className="flex justify-between items-center md:hidden">
          <div className="text-xl font-bold">MySite</div>
          <Sheet open={isOpen} onOpenChange={setIsOpen}>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon">
                <Menu className="w-6 h-6" />
              </Button>
            </SheetTrigger>
            <SheetContent side="left" className="w-64">
              <div className="flex flex-col space-y-4 mt-6">
                <Link href="/" onClick={() => setIsOpen(false)}>
                  Home
                </Link>
                <Link href="/web-design" onClick={() => setIsOpen(false)}>
                  Web Design
                </Link>
                <Link href="/seo" onClick={() => setIsOpen(false)}>
                  SEO
                </Link>
                <Link href="/about" onClick={() => setIsOpen(false)}>
                  About
                </Link>
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </nav>
  );
}
