import { Monitor } from "lucide-react";
import Link from "next/link";
import { Fragment } from "react";

const capabilities = [
  {
    Design: {
      title: "Design",
      items: [
        {
          name: "Web Design",
          link: "#",
          icon: <Monitor  className="w-[15px] h-[15px] text-gray-500"/>,
        },
        {
          name: "Design Systems",
          link: "#",
          icon: "",
        },
        {
          name: "Illustration Design",
          link: "#",
          icon: "",
        },
        {
          name: "Motion Design",
          link: "#",
          icon: "",
        },
        {
          name: "Branding",
          link: "#",
          icon: "",
        },
      ],
    },

    Development: {
      title: "Development",
      items: [
        {
          name: "Frontend Development",
          link: "#",
          icon: "",
        },
        {
          name: "System Integrations",
          link: "#",
          icon: "",
        },
        {
          name: "Backend Development",
          link: "#",
          icon: "",
        },
        {
          name: "Technical QA",
          link: "#",
          icon: "",
        },
        {
          name: "CMS Implementation",
          link: "#",
          icon: "",
        },
      ],
    },

    SEO: {
      title: "SEO",
      items: [
        {
          name: "Site Structure",
          link: "#",
          icon: "",
        },
        {
          name: "On-Page SEO",
          link: "#",
          icon: "",
        },
        {
          name: "Technical SEO",
          link: "#",
          icon: "",
        },
        {
          name: "Localization",
          link: "#",
          icon: "",
        },
      ],
    },
  },
];

export default function Capabilities() {
  return (
    <Fragment>
      <div className="w-40">
        <h4 className="font-semibold">{capabilities[0].Design.title}</h4>
        {capabilities[0].Design.items.map((item, index) => (
          <div className="flex flex-row gap-2">
            {item.icon}
            <Link href={item.link} key={index} className="block text-sm ">
              {item.name}
            </Link>
          </div>
        ))}
      </div>
      <div>
        <h4 className="font-semibold">{capabilities[0].Development.title}</h4>
        {capabilities[0].Development.items.map((item, index) => (
          <Link href={item.link} key={index} className="block text-sm ">
            {item.name}
          </Link>
        ))}
      </div>
      <div>
        <h4 className="font-semibold">{capabilities[0].SEO.title}</h4>
        {capabilities[0].SEO.items.map((item, index) => (
          <Link href={item.link} key={index} className="block text-sm ">
            {item.name}
          </Link>
        ))}
      </div>
    </Fragment>
  );
}
